@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Outfit", serif;
}

a {
  text-decoration: none;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: rgba(0, 255, 246.5, 0.5);
  border-radius: 10px;
  background-image: url("/images/icon-view.svg");
  background-repeat: no-repeat;
  background-position: center;
}

body {
  margin: 50px auto;
  background-color: #0d192b;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
body .card {
  box-shadow: 0px 7px 10px 3px hsl(216, 50%, 16%);
  width: 350px;
  height: 595px;
  background-color: hsl(216, 50%, 16%);
  padding: 25px;
  display: grid;
  gap: 25px;
  border-radius: 10px;
}
body .card hr {
  background-color: #2f415b;
  border: 0.5px #2f415b;
  height: 0.5px;
}
body .card__header {
  position: relative;
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
}
body .card__header__img-head {
  width: 300px;
  height: auto;
  border-radius: 10px;
  display: block;
}
body .card__header:hover .overlay {
  opacity: 1;
}
body .card__description {
  display: grid;
  gap: 25px;
}
body .card__description h2 {
  font-weight: 600;
  font-size: 22px;
  color: hsl(0, 0%, 100%);
}
body .card__description h2 a {
  font-weight: 600;
  font-size: 22px;
  color: hsl(0, 0%, 100%);
}
body .card__description h2 a:is(:hover, :focus) {
  cursor: pointer;
  color: hsl(178, 100%, 50%);
}
body .card__description p {
  font-size: 16px;
  line-height: 20px;
  color: hsl(215, 32%, 27%);
}
body .card__description__info {
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
body .card__description__info__ETH {
  display: flex;
  align-items: center;
  gap: 5px;
  color: hsl(178, 100%, 50%);
}
body .card__description__info__time-left {
  display: flex;
  align-items: center;
  gap: 5px;
  color: hsl(215, 51%, 70%);
}
body .card .card__creator {
  display: flex;
  align-items: center;
  gap: 15px;
}
body .card .card__creator .card__creator__img {
  width: 30px;
  height: 30px;
  border: 1px solid hsl(0, 0%, 100%);
  border-radius: 25px;
}
body .card .card__creator .card__creator__whos {
  font-size: 12px;
  color: hsl(215, 32%, 27%);
}
body .card .card__creator .card__creator__whos__name a {
  font-size: 12px;
  color: hsl(0, 0%, 100%);
}
body .card .card__creator .card__creator__whos__name a:is(:hover, :focus) {
  cursor: pointer;
  color: hsl(178, 100%, 50%);
}